@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800,900);
html,
body {
  font: normal 400 16px/150% 'Nunito', sans-serif;
  color: #343c59;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@media screen and (min-width: 1200px) {
  font: normal 400 18px/150% 'Nunito', sans-serif; }

h1, .h1,
h1 a, .h1 a {
  margin-bottom: 2rem;
  font-family: 'Nunito', sans-serif;
  font-size: 2rem;
  font-weight: 400;
  color: #725EBF; }

h2, .h2,
h2 a, .h2 a {
  margin-bottom: 1rem;
  font-family: 'Nunito', sans-serif;
  line-height: 150%;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #5fb8d3; }

h3, .h3,
h3 a, .h3 a {
  margin-bottom: 1rem;
  font-size: 1rem;
  font-weight: 600; }

p.lead {
  font-family: 'Nunito';
  font-weight: 400;
  color: #343c59; }

p a {
  text-decoration: underline; }

small {
  font-size: 0.9rem;
  font-weight: 500;
  color: #343c59; }

a {
  color: #725EBF;
  -webkit-transition: color 0.5s ease;
  transition: color 0.5s ease;
  text-decoration: underline; }
  a:hover {
    color: #5fb8d3; }

ul.list-unstyled li {
  margin: 0.5rem 0; }

ul .list-inline-item:not(:last-child) {
  margin-right: 1.5rem; }

.btn {
  padding: .25rem 1rem;
  text-decoration: none; }

.btn-primary:not(:disabled):not(.disabled) {
  background: #725EBF;
  border: 2px solid #725EBF;
  color: #fff;
  font-weight: 600; }
  .btn-primary:not(:disabled):not(.disabled):hover, .btn-primary:not(:disabled):not(.disabled).active {
    background: #5fb8d3;
    border-color: #5fb8d3; }

.btn-secondary:not(:disabled):not(.disabled) {
  background: transparent;
  border: 2px solid #725EBF;
  color: #725EBF;
  font-weight: 600; }
  .btn-secondary:not(:disabled):not(.disabled):hover, .btn-secondary:not(:disabled):not(.disabled).active {
    background: transparent;
    border-color: #5fb8d3;
    color: #5fb8d3; }

.btn-tertiary {
  background: transparent;
  border: 2px solid #5fb8d3;
  color: #5fb8d3;
  font-weight: 600; }
  .btn-tertiary:hover {
    background: #5fb8d3;
    border-color: #5fb8d3;
    color: #fff; }

.btn-white {
  background: transparent;
  border: 2px solid #fff;
  color: #fff;
  font-weight: 600; }
  .btn-white:hover {
    background: #fff;
    border-color: #fff;
    color: #725EBF; }

.badge {
  padding: 4px 0.5rem 2px;
  border: 2px solid #fff;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.15); }

.badge-primary {
  background: #725EBF; }

.input-group .input-group-text {
  background: #fff; }

/**
* Gradients
*/
.bg-gradient {
  background: -webkit-linear-gradient(135deg, rgba(155, 211, 228, 0.5), rgba(194, 185, 227, 0.5), white);
  background: linear-gradient(-45deg, rgba(155, 211, 228, 0.5), rgba(194, 185, 227, 0.5), white);
  background-size: 400%;
  -webkit-animation: Gradient 10s ease infinite;
  animation: Gradient 10s ease infinite;
  box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.1); }

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

/**
* Colors
*/
.bg-gray {
  background: #fcfcfc; }

.carousel {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  white-space: nowrap; }
  .carousel .slider-wrapper {
    position: relative;
    width: 100%;
    height: calc(100% - 70px);
    -webkit-transition: -webkit-transform ease-out 0.45s;
    transition: -webkit-transform ease-out 0.45s;
    transition: transform ease-out 0.45s;
    transition: transform ease-out 0.45s, -webkit-transform ease-out 0.45s; }
    .carousel .slider-wrapper .slide {
      display: inline-block;
      height: 100%;
      width: 100%;
      box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.1); }
      .carousel .slider-wrapper .slide img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
  .carousel .carousel-arrows {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 70px;
    background: #fff; }
    .carousel .carousel-arrows .back-arrow,
    .carousel .carousel-arrows .next-arrow {
      position: absolute;
      margin: calc(1rem - 2px) 1rem;
      padding: 8px 10px;
      border: 1px dashed rgba(114, 94, 191, 0.5);
      border-radius: 100%;
      width: 40px;
      height: 40px;
      font-size: 1.25rem;
      color: #725EBF;
      cursor: pointer;
      -webkit-transition: all 0.25s ease;
      transition: all 0.25s ease; }
      .carousel .carousel-arrows .back-arrow:hover,
      .carousel .carousel-arrows .next-arrow:hover {
        background: #5fb8d3;
        color: #fff;
        border-color: #5fb8d3; }
    .carousel .carousel-arrows .back-arrow {
      left: 0; }
    .carousel .carousel-arrows .next-arrow {
      right: 0; }

.header {
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 9;
  width: 100%;
  height: 70px;
  top: 0;
  padding: 1rem 0;
  background: #fff;
  border-top: 1px solid rgba(114, 94, 191, 0.1); }
  .header .logo,
  .header .logo a {
    font-size: 1.5rem;
    letter-spacing: 2px;
    color: #725EBF;
    text-decoration: none; }
  .header .btn {
    padding: 0.1rem 0.5rem; }
  .header .nav-link {
    text-decoration: none;
    color: #555; }
    .header .nav-link:hover {
      text-decoration: none; }

.hero {
  position: relative;
  top: 70px;
  left: 0;
  width: 100%;
  height: calc(100vh - 70px);
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media screen and (min-width: 768px) {
    .hero {
      position: fixed;
      left: 0;
      width: 30%; } }
  .hero h1 {
    font-size: 1.5rem; }
    .hero h1 span {
      display: block;
      margin: 0 0 1rem;
      font-size: 1rem;
      font-weight: 800;
      text-transform: uppercase;
      opacity: 0.75; }
  .hero ul {
    opacity: 0.75; }

.content {
  position: relative;
  top: 70px;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #fcfcfc;
  overflow: auto; }
  @media screen and (min-width: 768px) {
    .content {
      position: fixed;
      left: 30%;
      width: 70%;
      height: calc(100vh - 70px);
      border-left: 10px solid #fff; } }
  .content article {
    display: none; }

